@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;1,300&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Lato",sans-serif}.App{transition:all .5s ease}.library-active{margin-left:30%}h1{font-size:1rem}h2{color:#333}h3,h4{color:#646464;font-weight:400}.library{position:fixed;box-shadow:2px 2px 50px #ddd;border-radius:1rem;top:0;left:0;width:20rem;height:100%;overflow:scroll;background:#fff;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all .5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer;transition:all .75s ease-out}.library-song img{width:30%}.library-song:hover{background:#ebebeb}.song-description{padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,.7) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,.7);border-radius:20px;border:transparent}.selected{background:#c2d0fd}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav h1{font-family:"Poppins",sans-serif}nav button{background:transparent;border:none;cursor:pointer;padding:.5rem;border:2px solid #414141;transition:all .3s ease}nav button:hover{background:#414141;color:#fff}nav .library-active{background:#414141;color:#fff}@media screen and (max-width: 768px){nav button{z-index:10}}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex;align-items:center}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:40%}.play-control svg{cursor:pointer}.track{width:100%;height:1rem;position:relative;overflow:hidden;border-radius:1rem}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(0%);transform:translateX(0%);padding:1rem;pointer-events:none}input[type=range]:focus{outline:none}input[type=range]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:80%}}.song-container{min-height:60vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.song-container img{width:25%;border-radius:50%;transition:all 2s ease}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 768px){.song-container img{width:60%}}@-webkit-keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.rotateSong{-webkit-animation:rotate 20s linear forwards infinite;animation:rotate 20s linear forwards infinite}
